@import "./reset/basic-reset";

$grey : #f7f7f7;

section {
    background: $grey;
    min-height: 300px;
    padding: 20px;
}

section:nth-child(even) {
    background: #e8e8e8;
}




/* FOOTER
 ****************************************************************************************************************/
footer {
    background-color: #505050;
    color: #cccccc;
    font-size: 16px;
    margin: 0;
    font-weight: 300;
    /*	-webkit-font-smoothing: antialiased; */
}

footer .upper {
    padding: 10px 15px;
}

footer a {
    color: #cccccc;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

footer a:hover,
footer a:focus,
footer a:hover span,
footer a:focus span {
    text-decoration: underline;
    color: inherit;
    color: #ffffff;
}

footer h1, footer h2, footer h3 {
    color: #ffffff;
}

footer h2 {
    font-size: 24px;
    line-height: 26px;
    margin: 7px 0;
    padding: 8px 0;
}

footer .kaplan-copyright {
    text-align: right;
}

footer ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

footer ul li {

    float: left;
}

footer .footer-1row-col {
    padding-bottom: 20px;
}

footer .social-container .social-icon {
    display: block;
    float: left;
    width: 30px;
    height: 30px;
    margin-right: 8px;
    text-indent: 9999px;
    white-space: nowrap;
    overflow: hidden;
    border-radius: 15px;
    box-shadow: 1px 0px 2px 0px rgba(0, 0, 0, 0.3);
}

/* Arabic */
:lang(ar) footer .social-container .social-icon, :lang(ar-SA) footer .social-container .social-icon {
    float: right;
    margin: 0 0 0 8px;
}

footer .social-container a:hover .social-icon {
    background-position-x: -30px;
    box-shadow: none;
}

footer .social-container a .facebook {
    background: url(../images/kic-socials.png) -60px 0 no-repeat;
}

footer .social-container a .twitter {
    background: url(../images/kic-socials.png) -60px -30px no-repeat;
}

footer .social-container a .youtube {
    background: url(../images/kic-socials.png) -60px -60px no-repeat;
}

footer .social-container a .instagram {
    background: url(../images/kic-socials.png) -60px -90px no-repeat;
}

footer .social-container a .googleplus {
    background: url(../images/kic-socials.png) -60px -120px no-repeat;
}

footer .social-container a .vk, footer .social-container a .vkontakte {
    background: url(../images/kic-socials.png) -60px -150px no-repeat;
}

footer .social-container a .weibo {
    background: url(../images/kic-socials.png) -60px -180px no-repeat;
}

footer .social-container a .youku {
    background: url(../images/kic-socials.png) -60px -210px no-repeat;
}

footer .social-container a .kaplanblog {
    background: url(../images/kic-socials.png) -60px -240px no-repeat;
}

footer .social-container a .wechat {
    background: url(../images/kic-socials.png) -60px -270px no-repeat;
    cursor: pointer;
}

footer .social-container .text {
    line-height: 30px;
}

footer .subfooter {
    background-color: #303030;
    font-size: 12px;
    color: #cccccc;
    padding: 16px 0 30px 0;
}

footer .subfooter .col-md-3 {
    text-align: right;
    padding-top: 18px;
}

/* Footer: accreditation and partners */
footer .accreditation-container {
    margin-top: 20px;
    margin-bottom: 20px;
    border-top: 1px solid #404040;
}

footer .accreditation-container h4.block-title {
    font-size: 16px;
    color: #ffffff;
}

footer .accreditation-title {
    border-top: 1px solid #606060;
}

footer .footer-accreditation-item h4 {
    font-size: 14px;
    color: #aaaaaa;
}

footer .accreditation-logo {
    padding: 4px 0;
}

footer .accreditation-logo img {
    opacity: 0.5;
    filter: alpha(opacity=50);
    -webkit-transform-origin-x: 0;
    -moz-transform-origin-x: 0;
    -o-transform-origin-x: 0;
    -ms-transform-origin-x: 0;
    transform-origin-x: 0;
    -webkit-transform-origin-y: 0;
    -moz-transform-origin-y: 0;
    -o-transform-origin-y: 0;
    -ms-transform-origin-y: 0;
    transform-origin-y: 0;
}

footer .accreditation-logo img:hover {
    opacity: 1;
    filter: alpha(opacity=100);
}

/* Small devices (tablets, 768px and up) */
@media (max-width: 992px) {
    .globe {

        float: right;
        margin-right: 20px;
    }

    /* FELXIBLE DIV COLUMNS */
    .flex-col-three, .flex-col-four, .flex-col-five, .flex-col-six {
        margin: auto;
        width: 100%
    }

    /* FOOTER
    ****************************************************************************************************************/
    footer .kaplan-copyright {
        text-align: center;
    }

    footer .footer-1row-col {
        padding-bottom: 20px;
        border-top: 1px dotted #707070;
    }

    footer .footer-1row-col:first-child {
        border: none;
    }

    footer.footer ul li {
        display: inline-block;
        padding-right: 6px;
        padding-top: 3px;
        padding-bottom: 3px;
    }

    /* Arabic */
    :lang(ar) body footer.footer ul li, :lang(ar-SA) body footer.footer ul li {
        padding-right: 0px;
    }

    footer.footer ul li:after {
        content: '|';
        padding-left: 6px;
        color: #909090;
    }

    /* Arabic */
    :lang(ar) body footer.footer ul li:after {
        padding-left: 0px;
        padding-right: 6px;
    }

    footer .kaplan-address, footer .kaplan-copyright {
        padding-bottom: 10px;
        color: #aaaaaa;
        text-align: center;
    }

    footer.footer .social-container .social-icon {
        margin-right: 4px;
    }

    /* Arabic */
    :lang(ar) body footer.footer .social-container .social-icon, :lang(ar-SA) body footer.footer .social-container .social-icon {
        margin-right: 0px;
        margin-left: 4px;
    }

    footer.footer .social-container .text {
        display: none;
    }

    footer.footer ul.social-container li {
        padding-right: 0;
        float: none;
        display: inline-block;
    }

    footer.footer ul.social-container li:after {
        content: none;
    }

    /* Footer: accreditation and partners */
    footer .accreditation-container {
        display: none;
    }

    .social-container {
        width: 100%;
        text-align: center;
    }

    footer h2 {
        display: block;
        width: 100%;
        text-align: center;
        float: left;
    }

}

/* Medium devices (desktops small res, 992px and up) */
@media (min-width: 992px) {

    /* FOOTER
    ****************************************************************************************************************/
    .footer ul li {
        padding: 3px 0;
    }

    /* Footer: accreditation and partners */
    footer .accreditation-logo a img {
        -webkit-transform: scale(0.75);
        -ms-transform: scale(0.75);
        transform: scale(0.75);
    }
}

/* Large devices (desktops large res, 1200px and up) */
@media (min-width: 1200px) {

    /* FOOTER
    ****************************************************************************************************************/
    /* Footer: accreditation and partners */
    footer .accreditation-logo a img {
        -webkit-transform: scale(1);
        -ms-transform: scale(1);
        transform: scale(1);
    }
}

